import React, { useEffect } from "react";
import Tabledark from "../../common/components/table/table";
// import Datetime  from "../../common/components/datetimepicker/datetimepick";
import { useState } from "react";
import Table from "react-bootstrap/Table";
import axios from "axios";
const Create = () => {
  // const [value, onChange] = useState(new Date());
  const [data, setData] = useState([]);
  useEffect(() => {
    allusers();
  }, []);
  const allusers = async () => {
    try {
      const res = await axios.get("/api/allusers");
      if (res.status === 200) {
        setData(res.data);
      } else window.alert("server error in fetching data");
    } catch (error) {
      console.log(error);
    }
  };

  console.log(data);

  return (
    <>
      <div className="container my-5">
        <h1 className={"text-success"}>Applicants</h1>

        <Table responsive="sm" striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>#</th>
              <th>Candidate Name</th>
              <th>Email</th>
              <th>D.O.B</th>
              <th>Date Created</th>
              <th>Action</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {data.map((data, ind) => {
              // return <Product key={data.id} img={data.img} name={data.name} about={data.about} id={data.id}/>
              return (
                <Tabledark
                  key={data._id}
                  ind={ind + 1}
                  name={data.name}
                  email={data.email}
                  dob={data.dob}
                  created={data.date}
                  id={data._id}
                />
              );
            })}
            ;
          </tbody>
        </Table>
        {/* <Datetime/> */}
      </div>
    </>
  );
};
export default Create;
