import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

const Apply = () => {
  const [user, setUser] = useState({
    name: "",
    email: "",
    dob: "",
    password: "",
    cpassword: "",
  });

  let name, value;
  const handleInput = (e) => {
    name = e.target.name;
    value = e.target.value;
    setUser({ ...user, [name]: value });
  };
  const submitdata = async (e) => {
    e.preventDefault();
    const { name, email, dob, password, cpassword } = user;
    const res = await fetch("/api/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        email,
        dob,
        password,
        cpassword,
      }),
    });
    // const data = await res.json();
    if (res.status === 200) {
      window.alert(
        `Hello, ${user.name}! Congrats have registered successfully!`
      );
    } else {
      window.alert("some error occured");
    }
  };

  return (
    <>
      <div className="container">
        <h1 className={"text-success my-2"}>Apply Now</h1>
        <Form method="post" className="my-3">
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control
              name="email"
              type="email"
              value={user.email}
              onChange={handleInput}
              placeholder="Enter email"
            />
            <Form.Text className="text-muted">
              We'll never share your email with anyone else.
            </Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Name</Form.Label>
            <Form.Control
              name="name"
              type="text"
              value={user.name}
              onChange={handleInput}
              placeholder="Enter your name"
            />
            <Form.Text className="text-muted">Full Name</Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>DOB:</Form.Label>
            <Form.Control
              name="dob"
              type="date"
              value={user.dob}
              onChange={handleInput}
              placeholder="Enter your date of birth"
            />
            <Form.Text className="text-muted">DOB</Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Password</Form.Label>
            <Form.Control
              name="password"
              type="password"
              value={user.password}
              onChange={handleInput}
              placeholder="enter your password"
            />
            <Form.Text className="text-muted">use a strong password</Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control
              name="cpassword"
              type="password"
              value={user.cpassword}
              onChange={handleInput}
              placeholder="confirm your password"
            />
            <Form.Text className="text-muted">repeat your password</Form.Text>
          </Form.Group>

          <Button variant="primary" type="submit" onClick={submitdata}>
            Submit
          </Button>
        </Form>
      </div>
    </>
  );
};
export default Apply;
